/* Load the user-specified, or default fonts */





$site-font-stack-base: 'sans-serif', sans-serif;
$site-font-stack-head: 'sans-serif', sans-serif;
$site-font-stack-mono: 'monospace', monospace;


/* Pass information from Jekyll to SASS */

$site-tagline: 34;
$site-tooltips: enabled;
$site-hover-light: disabled;
$site-sidebar-side: left;

/* Now load rest of the modules -- @import order matters!! */

@import 'libs.scss';
@import 'custom.scss';

@import 'partials/base';
@import 'partials/palette';
@import 'partials/menu';
@import 'partials/tooltips';
@import 'partials/highlight';
@import 'partials/typography';



  
    
      @import 'partials/site-header';
    
  

  
    
      @import 'partials/site-footer';
    
  

  
    
      @import 'partials/site-sidebar';
    
  


@import 'partials/layout';
